#main-menu {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: center;
    text-align: center;
    > section > div.icon-button {
        margin: 5px; }
    > div.Logo {
        position: absolute;
        top: 4.5rem;
        > h1 {
          margin: 0;
          font-size: 3rem; }
        > p {
          margin: 0;
          font-size: 1.5rem; } }
    > div.main {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-content: center;
        align-items: center;
        > .normal-button {
            margin: 8px 0;
            padding: 5px 15px;
            font-size: 18px; } }
    #menu-nav {
        position: absolute;
        bottom: 1rem; } }
