.back-button {
    color: black;
    font-weight: bold;
    border-radius: 100%;
    border: none;
    width: 3rem;
    height: 3rem;
    background: rgba(209,209,209,0.7);
    text-align: center;
    font-size: 25px;
    outline: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    > img {
        outline: none; } }
.backbutton:active {
    transform: scale(1.1); }
.backbutton:hover {
    transform: scale(1.1); }
