#about {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: center;
    > section {
        margin: 4rem;
        text-align: center; }
    > .back-button {
      position: absolute;
      top: 4rem;
      left: 4rem; } }

@media (max-width: 768px) {
   #about {
     > .back-button {
      top: 2rem;
      left: 1rem; } } }
