@import '../_pallet';
.normal-button {
    border: none;
    padding: 5px 10px;
    border-radius: 10px;
    background: $blue_one_RGBA;
    color: white;
    user-select: none;
    font-family: "Polytopia"; }
.normal-button:active {
    transform: scale(1.2); }
.normal-button:hover {
    color: black;
    background: rgba(256,256,256,0.75); }
.normal-button.warn {
         background: red;
         color: white; }
.normal-button.disable {
        background: lightgray;
        color: black; }
