@import '../_pallet';

.toggle-button {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; }

.toggle-button > h5 {
  margin: 0 0 .3rem 0; }

.tgl {
  display: none; }

.tgl,
.tgl:after,
.tgl:before,
.tgl *,
.tgl *:after,
.tgl *:before,
.tgl + .tgl-btn {
  box-sizing: border-box; }

.tgl::-moz-selection,
.tgl:after::-moz-selection,
.tgl:before::-moz-selection,
.tgl *::-moz-selection,
.tgl *:after::-moz-selection,
.tgl *:before::-moz-selection,
.tgl + .tgl-btn::-moz-selection {
  background: none; }

.tgl::selection,
.tgl:after::selection,
.tgl:before::selection,
.tgl *::selection,
.tgl *:after::selection,
.tgl *:before::selection,
.tgl + .tgl-btn::selection {
  background: none; }

.tgl + .tgl-btn {
  outline: 0;
  display: block;
  width: 4em;
  height: 2em;
  position: relative;
  cursor: pointer;
  user-select: none; }

.tgl + .tgl-btn:after,
.tgl + .tgl-btn:before {
  position: relative;
  display: block;
  content: "";
  width: 50%;
  height: 100%; }

.tgl + .tgl-btn:after {
  left: 0; }

.tgl + .tgl-btn:before {
  display: none; }


.tgl-skewed + .tgl-btn {
  overflow: hidden;
  -webkit-transform: skew(-10deg);
  transform: skew(-10deg);
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transition: all 0.2s ease;
  font-family: sans-serif;
  background: red; }

.tgl-skewed + .tgl-btn:after,
.tgl-skewed + .tgl-btn:before {
  -webkit-transform: skew(10deg);
  transform: skew(10deg);
  display: inline-block;
  transition: all 0.2s ease;
  width: 100%;
  text-align: center;
  position: absolute;
  line-height: 2em;
  font-weight: bold;
  color: #fff;
  text-shadow: 0 1px 0 rgba(0, 0, 0, 0.4); }

.tgl-skewed + .tgl-btn:after {
  left: 100%;
  content: attr(data-tg-on); }

.tgl-skewed + .tgl-btn:before {
  left: 0;
  content: attr(data-tg-off); }

.tgl-skewed + .tgl-btn:active {
  background: red; }

.tgl-skewed + .tgl-btn:active:before {
  left: -10%; }

.tgl-skewed:checked + .tgl-btn {
  background: $blue_one_HEX; }

.tgl-skewed:checked + .tgl-btn:before {
  left: -100%; }

.tgl-skewed:checked + .tgl-btn:after {
  left: 0; }

.tgl-skewed:checked + .tgl-btn:active:after {
  left: 10%; }

