#throneroom {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center;
    > .back-button {
      position: absolute;
      top: 2rem;
      left: 1rem; }
    > section {
        display: flex;
        flex-direction: column;
        align-self: center;
        > h4 {
            align-self: center; }
        > .loader {
            align-self: center; } } }

@media (max-width: 768px) {
   #throneroom {
     > .back-button {
      top: 4rem;
      left: 1rem; } } }
