@import '../../_pallet.sass';
.slidecontainer {
  width: 100%; }

#audio {
  width: 100%;
  height: 30px;
  border-radius: 25px;
  background: $darkgray_bg;
  outline: none;
  opacity: 0.9;
  -webkit-transition: .2s;
  transition: opacity .2s; }
#audio:hover {
  opacity: 1; }
#audio::-webkit-slider-thumb {
  appearance: none;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: $blue_one_HEX;
  border: none;
  cursor: pointer; }
#audio::-moz-range-thumb {
  width: 30px;
  height: 30px;
  border: none;
  border-radius: 50%;
  background: $blue_one_HEX;
  cursor: pointer; }
#settings {
  position: absolute;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
  background: $darkgray_bg;
  > .back-button {
      position: absolute;
      top: 4rem;
      left: 4rem; }
  > h1 {
    position: absolute;
    top: 1rem; }
  > hr {
    width: 10rem;
    height: 2px;
    background: lightgray; }
  > section {
      display: flex;
      > .toggle-button {
        margin-left: 10px; } }
  > #audio-select {
      display: flex;
      flex-flow: column;
      justify-content: center;
      align-content: center; } }


@media (max-width: 768px) {
   #settings {
     > .back-button {
      top: 2rem;
      left: 1rem; } } }

