.icon-button {
    color: white;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    > button {
        width: 3rem;
        height: 3rem;
        border-radius: 100%;
        border-color: white;
        border: 2px solid white;
        background-color: rgba(89,89,89,0.5);
        cursor: pointer;
        > img {
            width: 1.7rem;
            height: 1.7rem; } }
    > button:active {
        transform: scale(1.1); }
    > p {
        margin: 0;
        font-size: 0.7rem;
        cursor: pointer; } }




